.form-accordion__block {
  display: none;
}

.form-accordion__item--active .form-accordion__block,
.form-accordion__item--nojs .form-accordion__block {
  display: flex;
  flex-direction: column;
  padding: 0 25px 28px 25px;
}

.form-accordion__item--active .form-accordion__toggle {
  transform: rotate(180deg);
}

.form-accordion__item--nojs .form-accordion__toggle {
  background-image: none;
}
